<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- header -->
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <div class="pl-4">
                  <i class="far fa-list-alt"></i>
                  <h5 class="pl-2 d-inline-block">
                    <strong>SUCCESSFUL ORDERS</strong>
                  </h5>
                </div>
              </div>
              <div class="col-6 content-header small">
                <span class="float-right">
                  <router-link to="/" class="cursor-pointer" tag="span">
                    dashboard
                  </router-link>
                  >
                  <router-link
                    to="/order/dashboard"
                    class="cursor-pointer"
                    tag="span"
                  >
                    sales order
                  </router-link>
                  >
                  <router-link
                    to="/order/return/success"
                    class="cursor-pointer"
                    tag="span"
                  >
                    successful
                  </router-link>
                </span>
              </div>
            </div>
            <hr class="pl-4 pr-4" />
            <!-- content -->
            <DashBoard
              :newOrders="orders"
              :disable="disable"
              :status="status"
              @fetchOrders="fetchOrders"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DashBoard from "./DashBoard.vue";
import { orderMixin } from "../../Mixins/orderMixin.js";

export default {
  name: "SuccessfulOrders",
  mixins: [orderMixin],

  components: {
    DashBoard
  },

  data() {
    return {
      status: 7
    };
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



